.NavbarItems {
    background: linear-gradient(90deg, rgb(74,151,255) 0%, rgba(73,63,252,1) 100%);
    height: 95px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.1rem;
}

.icon {
    color: #fff;
    justify-self:start;
    margin-left: 20px;
    cursor: pointer;
}

.nav-menu {
    display: grid;
    grid-template-columns: repeat(5,auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 70vw;
    justify-content: end;
}

